:root {
  --gris:#244A55; }

::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--gris); }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #12353f; }

* {
  box-sizing: border-box; }

*::-moz-selection {
  background-color: var(--gris);
  color: #ffffff; }

*::selection {
  background-color: var(--gris);
  color: #ffffff; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

html {
  scroll-behavior: smooth;
  font-size: 62.5%; }

body {
  margin: 0;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  font-family: 'KoHo', sans-serif; }

.header {
  display: none; }

@media (min-width: 768px) {
  .header {
    background: linear-gradient(#ffffff, transparent);
    height: 6rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 2;
    width: 100%;
    position: fixed; } }

.logo__header,
.contenedor,
.imagen__contenedor2,
.prin,
.seg,
.ter,
.cua,
.productosim,
.contenedor4 {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }

.logo__header {
  background-image: url(../img/LOGO-02.png);
  height: 100%;
  width: 7rem;
  margin-left: .3rem;
  -webkit-animation: rot 2s infinite alternate;
  animation: rot 2s infinite alternate; }

@-webkit-keyframes rot {
  0% {
    transform: translate(0); }
  100% {
    transform: translate(5rem);
    opacity: 0; } }

@keyframes rot {
  0% {
    transform: translate(0); }
  100% {
    transform: translate(5rem);
    opacity: 0; } }

.header__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 2rem; }

@media (min-width: 768px) {
  .header__nav {
    flex-direction: row; } }

.header__nav a {
  padding: 1rem;
  color: #244A55; }

.header__nav a:hover {
  text-decoration: underline; }

.container {
  background-color: white; }

.contenedor,
.contenedorr,
.contenedor2,
.contenedor3,
.contenedor4 {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center; }

.contenedor {
  background-image: url(../img/fondoheader.jpg);
  outline: 0.3rem solid var(--gris);
  outline-offset: -1rem; }

@media (min-width: 768px) {
  .contenedor {
    outline: none; } }

.contenedor__text {
  position: relative;
  bottom: 7rem;
  margin: 2rem;
  max-width: 100%; }

@media (min-width: 768px) {
  .contenedor__text {
    max-width: 47%; } }

.contenedor__text h4 {
  font-size: 4rem;
  margin-bottom: 1rem; }

.contenedor__text span {
  color: var(--gris);
  font-size: 4.4rem; }

.contenedor2 {
  flex-direction: column; }

@media (min-width: 768px) {
  .contenedor2 {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 2fr 1fr;
    grid-template-columns: 2fr 1fr; } }

.texto__contenedor2 {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 80rem;
  margin: 0 4rem; }

.texto__contenedor2 h2 {
  color: var(--gris);
  font-size: 6rem;
  margin-bottom: 1rem; }

.texto__contenedor2 p {
  font-size: 2rem;
  height: 30rem; }

.imagen__contenedor2 {
  background-image: url(../img/fondomain.jpg);
  background-position: center right;
  margin-top: 2rem;
  height: 22rem;
  width: 100%;
  -webkit-clip-path: polygon(0 20%, 100% 0%, 100% 100%, 0% 100%);
  clip-path: polygon(0 20%, 100% 0%, 100% 100%, 0% 100%);
  margin-bottom: 8rem; }

@media (min-width: 768px) {
  .imagen__contenedor2 {
    background-position: center right;
    -webkit-clip-path: polygon(0 30%, 100% 0, 100% 68%, 0% 100%);
    clip-path: polygon(0 30%, 100% 0, 100% 68%, 0% 100%);
    height: 98vh;
    margin-bottom: 0; } }

.productos {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  flex-wrap: wrap; }

.producto {
  margin-bottom: 5rem; }

@media (min-width: 768px) {
  .producto:first-child {
    margin-left: 3rem; } }

@media (min-width: 768px) {
  .producto {
    border-left: 0.2rem solid rgba(104, 104, 104, 0.2);
    margin-bottom: 0; } }

@media (min-width: 768px) {
  .productos {
    justify-content: space-between; } }

.producto {
  max-width: 40rem;
  padding: 0 4rem;
  font-size: 1.8rem; }

.producto h5 {
  color: var(--gris);
  font-size: 2rem;
  margin: 1rem 0 .5rem 0; }

.productosim {
  background-image: url(../img/banco.jpg);
  width: 15em;
  height: 80vh;
  position: relative; }

.productosim::before {
  content: '';
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
  bottom: -1.5rem;
  right: -1.5rem;
  outline: 0.3rem solid var(--gris); }

@media (min-width: 768px) {
  .productosim {
    margin-left: 1rem;
    width: 40rem; } }

.productosim {
  background-position: center center; }

.Servicios {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 1rem auto;
  padding: 8rem 5rem;
  flex-wrap: wrap; }

.cuadro {
  height: 61vh;
  width: 48vh;
  background-color: #333;
  margin: 3rem 0;
  box-shadow: .5rem .5rem 1rem #000000;
  transition: transform 400ms;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end; }

@media (min-width: 768px) {
  .cuadro {
    height: 52vh;
    width: 39vh; } }

.cuadro::before {
  content: '';
  background-color: #0000009f;
  position: absolute;
  top: 31rem;
  left: 0;
  right: 0;
  bottom: 0; }

@media (min-width: 768px) {
  .cuadro::before {
    top: 27.5rem; } }

.cuadro:hover {
  transform: scale(1.05); }

.cuadro h4 {
  text-transform: uppercase;
  color: #ffffff;
  position: relative;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  margin: 0rem 1rem;
  bottom: 1.8rem; }

@media (min-width: 768px) {
  .cuadro h4 {
    font-size: 2rem; } }

.prin {
  background-image: url(../img/home.jpg); }

.seg {
  background-image: url(../img/inver.jpg); }

.ter {
  background-image: url(../img/car.jpg); }

.cua {
